<template>
    <div class="admin-user">
        <el-tabs type="border-card">
            <el-tab-pane label="">
                <el-table
                        :data="category"
                        stripe
                        style="width: 100%">
                    <el-table-column
                            prop="_id"
                            label="ID">
                    </el-table-column>
                    <el-table-column
                            prop="name"
                            label="">
                    </el-table-column>

                    <el-table-column
                            label="">
                        <template v-slot="scope">
                            <el-button size="mini" type="danger" @click="deleteCate(scope.row.name)"></el-button>
                            <el-button size="mini" type="primary" @click="updateCategory(scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>

            </el-tab-pane>
        </el-tabs>

        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
            <el-input v-model="input"></el-input>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="confirmUpdateCategory"> </el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    import {reqGetCategory, reqDelCategory, reqUpCategory} from '../../../../src/api/index'
    import {mapState} from 'vuex'
    export default {
        data() {
            return {
                dialogVisible: false,
                input: '',
                _id: ''
            }
        },
        computed:{
            ...mapState(['category'])
        },
        methods: {
            deleteCate(name){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(async () => {
                    const result = await reqDelCategory(name);
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                    this.$store.dispatch('getAllCategory')
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            handleClose(done) {
                this.$confirm('')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {});
            },
            // 
            updateCategory(data){
                const {_id, name} = data;
                this.dialogVisible = true;
                this.input = name;
                this._id = _id;

            },
            async confirmUpdateCategory(){
                const result = await reqUpCategory(this._id, this.input);
                if(result.code === 0){
                    this.$message({
                        message: '',
                    });
                    this.dialogVisible = false;
                    this.$store.dispatch('getAllCategory')
                }
            }
        },
        mounted(){
            this.$store.dispatch('getAllCategory')
        }
    };
</script>

<style scoped lang="less">
    .admin-user, .el-pagination{
        margin-top: 20px;
    }
</style>
